.nav {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: fit-content;
  box-sizing: border-box;
  padding: 0 50px;
  background-color: transparent;
  position: fixed;
  z-index: 999;
  background-color: #222222 !important;
}
.button {
  display: none;
}
.logo {
  width: 50%;
}
.logo h5 {
  font-size: 2em;
  font-weight: 550;
  color: whitesmoke;
  white-space: nowrap;
}
.links {
  width: 50%;
  display: flex;
  justify-content: flex-end;
}
a {
  text-decoration: none;
  color: lightslategray;
  font-size: 1.2em;
  transition: all 0.3s;
}
a:hover,
a:focus {
  background-color: lightslategray;
  color: #61dafb;
}
.links a {
  padding: 15px 25px;
  font-size: 1.12em;
}

@media (max-width: 576px) {
  .nav {
    padding: 0 10px;
    align-items: center;
    height: 9vh;
  }
  .links {
    position: fixed;
    width: 50vw;
    height: 100vh;
    top: 0;
    left: 0;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    transform: translateX(-100%);
    transition: all 0.5s;
  }
  .button {
    display: block;
  }
  .button .MuiSvgIcon-root {
    transition: all 0.3s;
  }
  .button .MuiSvgIcon-root:hover,
  .button .MuiSvgIcon-root:focus {
    transform: scale(1.22);
    display: inline-block;
  }
  .slide {
    transform: translateX(0) !important;
    background-color: #222222 !important;
  }
  .logo h5 {
    font-size: 1rem;
  }
}
