.grid-about {
  padding: 0 25px;
  box-sizing: border-box;
  height: 80vh;
  .img-about {
    width: 100%;
    padding: 25px;
    box-sizing: border-box;
  }
  @media screen and (max-width: 768px) {
    height: fit-content;
    .img-about {
      width: 80vw;
    }
    .text-about {
      margin: 20px 0 !important;
      text-align: justify;
      font-size: 13px;
    }
    h2 {
      font-size: 1.2rem !important;
    }
  }
}
.root {
  background-color: #222222 !important;
  color: whitesmoke;
  .paper-about {
    background-color: #222222 !important;
    color: whitesmoke;
  }
  @media screen and (max-width: 768px) {
    h1 {
      font-size: 2rem !important;
      padding-bottom: 10px;
    }
  }
}
